<template>
  <div>
    <Navbar></Navbar>
       <div class="login">
         <div class="login_page">
           <div class="login_main_title">
             {{$t('login')}}
           </div>
           <form @submit.prevent="login()">
             <div class="login_form_father">
               <div class="login_form">
                 <span>{{$t('email')}}</span>
                 <input type="email" v-model="email">
               </div>
             </div>
             <div class="login_form_father">
               <div class="login_form">
                 <span>{{$t('password')}}</span>
                 <input type="password" v-model="password">
               </div>
             </div>
             <div class="login_button">
               <button>
                 {{$t('login')}}
               </button>
             </div>
           </form>
           <div class="reg_link">
             <span>{{$t('reg')}}</span>
             <router-link to="/signup">{{$t('reg')}}</router-link>
           </div>
         </div>
       </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Navbar from "../../components/Header/Navbar";
import Footer from "../../components/Header/Footer";

export default {
  components: {Footer, Navbar},
  data(){
    return{
      password:'',
      email:''
    }
  },
  methods:{
    login(){
      this.$axiosDefault.post('/customers/login/',{
        "email":this.email,
        "password":this.password
      })
          .then(res=>{
            localStorage.setItem('reg_token',res.data.access)
            this.$store.dispatch('getMe')
            this.$router.push('/')

          })
          .catch(()=>{
            this.$toast("Email yoki parol xato ", {
              timeout: 2000,
              type:"error"
            });
          })

    }
  }
}
</script>
