<script>
import Navbar from "../../components/Header/Navbar";
import Footer from "../../components/Header/Footer";
import Card from "../../components/Card";

export default {
  components: {Card, Footer, Navbar},
  data(){
    return{
      items:1,
      sum:650000
    }
  },
  methods:{
   count(item){
     if(item=='add'){
       //  this.items=this.items+1
       // this.sum=this.sum*this.items
       this.$toast("Bizning bazamizda 1 ta mahsulot bor", {
         timeout: 2000,
         type:"error"
       });
     }
     if(item=='minus'){
       if(this.items>1){
         this.items=this.items-1
         this.sum=this.sum/this.items
       }
     }
   }
  }
}
</script>
