<template>
  <div>
    <Navbar></Navbar>
    <div class="login">
      <div class="login_page">
        <div class="login_main_title">
          {{$t('reg')}}
        </div>
        <form @submit.prevent="signUp()">
          <div class="login_form_father">
            <div class="login_form">
              <span>{{$t('first_name')}}</span>
              <input type="text" v-model="first_name">
            </div>
          </div>
          <div class="login_form_father">
            <div class="login_form">
              <span>{{$t('last_name')}}</span>
              <input type="text" v-model="last_name">
            </div>
          </div>
          <div class="login_form_father">
            <div class="login_form">
              <span>{{$t('email')}}</span>
              <input type="text" v-model="email">
            </div>
          </div>
          <div class="login_form_father">
            <div class="login_form">
              <span>{{$t('password')}}</span>
              <input type="password" v-model="password">
            </div>
          </div>
          <div class="login_button">
            <button>
              {{$t('reg')}}
            </button>
          </div>
        </form>
        <div class="reg_link">
          <span>{{$t('login')}}</span>
          <router-link to="/login">{{$t('login')}}</router-link>
        </div>
      </div>
    </div>
    <Footer></Footer>
  </div>
</template>
<script>
import Navbar from "../../components/Header/Navbar";
import Footer from "../../components/Header/Footer";
export default {
  components: {Footer, Navbar},
  data(){
    return{
      email:'',
      first_name:"",
      last_name:'',
      password:'',
    }
  },
  methods:{
    signUp() {
      this.$axiosDefault.post('/customers/register/', {
        "email": this.email,
        "first_name": this.first_name,
        "last_name": this.last_name,
        "password": this.password,
      })
          .then(() => {
            this.$toast("Emailga xabar jo'natildi tekshirib ko'ring", {
              timeout: 10000,
              type: "success"
            });
            this.$router.push('/verify')
          })
          .catch(() => {
            this.$toast("Ma'lumotlar hammasi kiritilmagan yoki bu telefon raqam oldin ro'yxatdan o'tgan", {
              timeout: 2000,
              type: "error"
            });
          })
    }
  }
}
</script>
